import * as THREE from 'three';
import vertexShader from './vert.glsl';
import fragmentShader from './frag.glsl';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader';

const importGLSL = (glsl: string) => `${glsl}`.replace(/#include < (.+) >/g, '#include <$1>');
const group = new THREE.Group();

const tex = new THREE.TextureLoader().load('/assets/low_noise.png');
tex.wrapT = tex.wrapS = THREE.RepeatWrapping;

const customMaterial = new THREE.ShaderMaterial({
  vertexShader: importGLSL(vertexShader),
  fragmentShader: /* glsl */ fragmentShader,
  side: THREE.DoubleSide,
  uniforms: {
    time: { value: 0 },
    tex: { value: tex }
  },
  transparent: true
});
customMaterial.needsUpdate = true;

const geometry = new THREE.SphereGeometry(1, 132, 132);
const meshs = new THREE.Mesh(geometry, customMaterial);
// meshs.rotation.x = Math.PI / 2;

const mesh2 = new THREE.Mesh(
  geometry,
  new THREE.MeshStandardMaterial({
    color: 'blue',
    // refractionRatio: 0.4
    roughness: 0.5,
    metalness: 1,
    envMapIntensity: 1
  })
);
mesh2.position.x += 3;

