import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader';

export class WebglEngine {
  public renderer: THREE.WebGLRenderer;
  public camera: THREE.PerspectiveCamera;
  public scene: THREE.Scene;
  public controls: OrbitControls;
  public pmremGenerator: THREE.PMREMGenerator;
  private cb: Function = () => {};
  private domElem: HTMLElement;

  constructor(domElem: HTMLElement) {
    this.domElem = domElem;
    console.log('ok', domElem);
    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.renderer.setSize(this.domElem.clientWidth, this.domElem.clientHeight);
    this.renderer.setPixelRatio(2);
    this.renderer.setClearColor('#fff');
    this.domElem.appendChild(this.renderer.domElement);
    this.camera = new THREE.PerspectiveCamera(75, 1, 0.1, 100000);
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color('white');
    this.pmremGenerator = new THREE.PMREMGenerator(this.renderer);
    this.pmremGenerator.compileEquirectangularShader();
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.sceneSetup();
    this.onWindowResize();
    window.addEventListener('resize', this.onWindowResize, false);
  }

  private onWindowResize = () => {
    this.camera.aspect = this.domElem.clientWidth / this.domElem.clientHeight;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(this.domElem.clientWidth, this.domElem.clientHeight);
  };

  private sceneSetup() {
    const light = new THREE.PointLight(new THREE.Color('white'), 15, 100);
    const light2 = new THREE.AmbientLight('white');
    light.position.set(50, 50, 50);
    // this.camera.position.set(1.5, 1.5, 1.5);
    this.camera.lookAt(0, 0, 0);
    this.scene.add(light, light2, this.camera);
    this.setEnviroment();
  }

  private render() {
    this.cb();
    this.renderer.render(this.scene, this.camera);
    requestAnimationFrame(() => this.render());
  }

  public animate(cb: Function) {
    this.cb = cb;
    this.render();
  }

  private setEnviroment = () => {
    // const envLoader = new RGBELoader().setDataType(THREE.UnsignedByteType);
    // envLoader.load('/assets/textures/spiaggia_di_mondello_1k.hdr', hdrenvtex => {
    //   const envMap = this.pmremGenerator.fromEquirectangular(hdrenvtex).texture;
    //   this.pmremGenerator.dispose();
    //   this.scene.environment = envMap;
    // });
  };
}
