import * as THREE from 'three';
import vertexShader from './vert.glsl';
import fragmentShader from './frag.glsl';

// const importGLSL = (glsl: string) => `${glsl}`.replace(/#include < (.+) >/g, '#include <$1>');

const tex = new THREE.TextureLoader().load('/assets/low_noise.png');
tex.wrapT = tex.wrapS = THREE.RepeatWrapping;
const ORANGE = new THREE.Color('#fed9c6');
const BLUE = new THREE.Color('#cdc0d4');

const genHalf = (col: THREE.Color) => {
  const customMaterial = new THREE.ShaderMaterial({
    vertexShader: vertexShader,
    fragmentShader: fragmentShader,
    side: THREE.DoubleSide,
    uniforms: {
      time: { value: 0 },
      tex: { value: tex },
      col: { value: col }
    },
    transparent: true
  });
  const meshs = new THREE.Mesh(new THREE.SphereGeometry(1, 32, 32), customMaterial);
  meshs.rotation.x = Math.PI / 2;
  return meshs;
};

const genSphere = () => {
  const mesh = new THREE.Mesh(
    new THREE.SphereGeometry(1, 32, 32),
    // https://threejs.org/docs/#api/en/materials/MeshPhysicalMaterial
    new THREE.MeshPhysicalMaterial({
      color: 'white',
      opacity: 0.1,
      metalness: 0.9,
      roughness: 0.05,
      envMapIntensity: 0.9,
      reflectivity: 0.2,
      refractionRatio: 0.985,
      ior: 0.9,
      side: THREE.DoubleSide,
      transparent: true
    })
  );
  mesh.scale.multiplyScalar(1.01);
  return mesh;
};

const half1 = genHalf(ORANGE);
const sphere1 = genSphere();
const half2 = genHalf(BLUE);
const sphere2 = genSphere();
half1.position.x += 1.25;
sphere1.position.x += 1.25;
half2.position.x -= 1.25;
sphere2.position.x -= 1.25;

interface IMesh {
  group: THREE.Object3D;
  update: Function;
}

const group = new THREE.Group();
group.add(half1, sphere1, half2, sphere2);

const blob: IMesh = {
  group,
  update: () => {
    half1.material.uniforms.time.value += 0.05;
    half2.material.uniforms.time.value += 0.05;
  }
};

export default blob;
