import './style.sass';
import * as THREE from 'three';
import { WebglEngine } from './webgl';
import halfempty from './halfempty';
import halfempty2 from './halfempty2';
// import halfempty from './flat';
import sphere from './sphere';
// import halfempty from './sphere-noise2dfunc';

const engine = new WebglEngine(document.querySelector('#sphere') as HTMLElement);
engine.camera.position.set(0, 1, -2);
engine.camera.lookAt(0, 0, 0);
engine.scene.add(sphere.group);
engine.animate(() => {
  sphere.update();
});

const engine2 = new WebglEngine(document.querySelector('#half') as HTMLElement);
engine2.camera.position.set(0, 1, -4);
engine2.camera.lookAt(halfempty.group.position);

engine2.scene.add(halfempty.group);
engine2.animate(() => {
  halfempty.update();
});

const engine3 = new WebglEngine(document.querySelector('#next') as HTMLElement);
engine3.camera.position.set(0, 1, -4);
engine3.camera.lookAt(halfempty2.group.position);

engine3.scene.add(halfempty2.group);
engine3.animate(() => {
  halfempty2.update();
});
